/* App.css */
.App {
  text-align: center;
}

header {
  background-color: #de2020;
  padding: 20px;
  color: white;
  display: flex;
  justify-content: flex-start; /* Align items horizontally to the start */
  align-items: center; /* Center items vertically */
}

nav {
  margin-left: auto; /* Push navigation to the right */
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li a {
  color: rgb(242, 242, 245);
  text-decoration: none;
}

nav ul li a:hover {
  color: #30885f; /* Change color on hover */
}

.app-name {
  margin: 0;
  font-size: 2rem;
  color: #44c24a; /* Set color for the app name */
}

.app-logo {
  width: 50px; /* Adjust size of logo */
  height: 50px; /* Adjust size of logo */
  border-radius: 50%; /* Make the logo circular */
  margin-right: 10px; /* Add space between logo and title */
}

.logout {
  margin-left: auto; /* Push logout button to the right */
}

.logout button {
  background-color: transparent;
  border: none;
  color: rgb(221, 219, 221);
  cursor: pointer;
  font-size: 1rem;
}

.logout button:hover {
  color: #1adf13; /* Change color on hover */
}